	module.exports = {"data":{"markdownRemark":{"html":"<p>If a key member of your security team goes on vacation or will be unavailable for a short period of time, be transparent with your hackers and let them know that your internal security resources will be limited for a bit. To notify hackers, you can:</p>\n<ol>\n<li>Use a trigger to set an automated response</li>\n<li>Update your Security Page by stating that response, triage, and resolution will take longer than normal</li>\n</ol>\n<p>It's best if you include as much information as possible and a timeline of when you expect your program to resume.</p>\n<p>HackerOne doesn't recommend that you pause submissions as it can be difficult to re-engage hackers after the pause ends. If you expect the disruption to last for a significant amount of time, please <a href=\"https://support.hackerone.com/hc/en-us/requests/new\">contact HackerOne</a> to discuss your situation.</p>","frontmatter":{"path":"/programs/vacations.html","id":"programs/vacations","title":"Vacations","description":null}}},"pathContext":{}}

